@import "../../scss/config";

.LoginPage {
  display: flex;
  justify-content: center;
  width: 100%;

  //&-Form {
  //  width: 100%;
  //}

  &-Field {
    &_password {
      margin-bottom: 36px;

      @media screen and (max-width: $break_sm - 1px){
        margin-bottom: 40px;
      }

      &.TextField_error {
        margin-bottom: 10px;

        @media screen and (max-width: $break_sm - 1px){
          margin-bottom: 24px;
        }
      }
    }
  }

  &-Button {
    &.Button {
      width: 100%;
    }
  }

  &-CreateAccount {
    display: flex;
    align-items: center;
    margin-top: 18px;

    @media screen and (max-width: $break_sm - 1px){
      margin-top: 9px;
    }
  }

  &-CreateAccountText {
    font-size: 12px;
    line-height: 22px;

    @media screen and (max-width: $break_sm - 1px){
      font-size: 10px;
      line-height: 16px;
    }
  }

  &-CreateAccountLink {
    margin-left: auto;
    font-size: 12px;
    line-height: 18px;
    text-decoration-line: underline;
    color: $black;

    @media screen and (max-width: $break_sm - 1px){
      font-size: 10px;
      line-height: 15px;
    }
  }

  &-Divider {
    &.Divider {
      margin-top: 37px;
      margin-bottom: 27px;

      @media screen and (max-width: $break_sm - 1px){
        margin-top: 24px;
        margin-bottom: 19px;
      }
    }
  }

  &-FastLogin {
    display: flex;
  }

  &-FastLoginButton {
    &.Button {
      display: flex;
      flex: 1 1 150px;
      margin: 0 8px;

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }

      @media screen and (max-width: $break_sm - 1px){
        height: 36px;
        margin: 0 4px;
      }

      .Button-Text {
        font-size: 14px;
        line-height: 21px;
      }
    }
  }

  &-FastLoginIcon {
    width: 24px;
    height: 24px;
    margin-right: 8px;

    @media screen and (max-width: $break_sm - 1px){
      margin-right: 0;
    }
  }
}