@import "../../scss/config";

.ProductCard {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-bottom: 16px;
  background-color: $light-gray;

  &-Image {
    &.ProductImage {
      margin-bottom: 16px;
    }
  }

  &-Button {
    &.SwitchAddRemoveButton {
      position: absolute;
      top: 0;
      right: 0;
      padding: 12px;
      z-index: 10;
    }
  }

  &-Props {
    margin-top: auto;
  }

  &-Name {
    width: 100%;
    margin-top: auto;
    padding: 0 16px;

    @media screen and (max-width: $break_sm - 1px){
      font-size: 10px;
      line-height: 16px;
    }
  }

  &-Price {
    width: 100%;
    padding: 0 16px;
    font-weight: 500;

    @media screen and (max-width: $break_sm - 1px){
      font-size: 10px;
      line-height: 16px;
    }
  }
}